<template>
    <el-row>
        <el-col :span="4">
            <div class="title">
                <el-date-picker
                        v-model="date"
                        type="date"
                        placeholder="选择日期"
                        format="yyyy MM dd "
                        value-format="yyyyMMdd">
                </el-date-picker>
            </div>
        </el-col>
        <el-col :span="7">
            <el-select v-model="type" placeholder="默认查询一小时数据"  @change="getData" filterable >
                <el-option v-for="s in typeData" :key="s.value" :value="s.value" :label="s.label"></el-option>
            </el-select>
            <el-button @click="getData">刷新数据</el-button>
        </el-col>
        <el-col :span="10">
            <div class="grid-content bg-purple-dark" style="text-align: right">
                <el-button type="primary" @click="handleEdit('','')">添加图表</el-button>
            </div>
        </el-col>
        <el-col :span="24">
            <div class="grid-content bg-purple-dark">
                <el-dialog
                        title="添加图表"
                        :visible.sync="dialogVisible"
                        width="60%"
                        :before-close="handleClose">
                    <span>
                        <el-select v-model="service" placeholder="Service" @change="serviceChange" filterable >
                            <el-option v-for="s in serviceOption" :key="s" :value="s" :label="s"></el-option>
                        </el-select>
                        <el-select  v-model="clazz" placeholder="Class" @change="clazzChange"  filterable>
                            <el-option v-for="s in clazzOption" :key="s" :value="s" :label="s"></el-option>
                        </el-select>
                        <el-select v-model="method" placeholder="Method" @change="methodChange" filterable >
                            <el-option v-for="s in methodOption" :key="s" :value="s" :label="s"></el-option>
                        </el-select>
                        <el-button @click="addChart">添加图表</el-button>

                    </span>

                </el-dialog>
            </div>
        </el-col>
        <el-col>

            <div class="data">
                <!--data chart-->
                <div class="chart-container">
                    <div class="chart-item" v-for="(c,i) in chartData" :key="c.name">
                        <i-echarts
                                :option="c.data"
                                :loading="c.loading">
                        </i-echarts>
                        <div><i class="el-icon-circle-close" style="margin-right: 5px;" @click="delChart(c, i)"></i><span>{{c.name}}</span></div>
                    </div>
                </div>
            </div>
        </el-col>

    </el-row>

</template>

<script>
import IEcharts from 'vue-echarts-v3/src/lite.js'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/dataZoom'
import 'echarts/lib/component/tooltip'

export default {
  name: 'statusChart',
  components: {
    IEcharts
  },
  data () {
    return {
      dialogVisible: false,
      date: '',
      type: '0',
      service: '',
      serviceOption: [],
      clazz: '',
      clazzOption: [],
      method: '',
      methodOption: [],
      chartData: [],
      typeData: [{
        value: '0',
        label: '一小时'
      },
      {
        value: '1',
        label: '一天'
      },
      {
        value: '2',
        label: '一周'
      }]
    }
  },
  methods: {
    handleEdit() {
      this.dialogVisible = true
    },
    handleClose () {
      this.dialogVisible = false
    },
    addChart() {
      const chartName = `${this.service}:${this.clazz}:${this.method}`
      this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      this.axios.get('/chart/addChartData', { params: { chartName } }).then((res) => {
        console.log(res)
        this.getData()
        this.$notify.success({
          title: 'Chart Added',
          content: `${chartName} added successfully`
        })
        this.dialogVisible = false
      })
    },
    delChart(item, i) {
      const chart = this.chartData.splice(i, 1)[0]
      // TODO:
      console.log(chart)
      this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      this.axios.get('/chart/deleteChartData', { params: { chartName: chart.name } }).then((res) => {
        console.log(res)
        this.$notify.success({
          title: 'Chart Deleted',
          content: `${chart.name} deleted successfully`
        })
        this.getData()
      })
    },
    serviceChange(val) {
      //
      this.clazzOption = []
      this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      this.axios.get('/chart/getBaseData', { params: { type: 1, serviceName: val } }).then((res) => {
        console.log(res)
        this.clazzOption = res.data
      })
    },
    clazzChange(val) {
      //
      this.methodOption = []
      this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      this.axios.get('/chart/getBaseData', { params: { type: 2, serviceName: this.service, clazz: val } }).then((res) => {
        console.log(res)
        this.methodOption = res.data
      })
    },
    methodChange() {
    },
    getData() {
      this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
      this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
      this.axios.get('/chart/getChartData', { params: { type: this.type, date: this.date } }).then((res) => {
        // TODO: get all table data
        console.log(res)
        this.chartData = res.data.map((v) => {
          return {
            name: v.name,
            data: {
              tooltip: {
                trigger: 'axis'
              },
              xAxis: {
                data: v.xaxis
              },
              yAxis: {},
              dataZoom: [{
                type: 'slider',
                start: 0,
                end: 100
              }],
              series: [{
                type: 'line',
                data: v.yaxis
              }]
            },
            loading: false
          }
        })
      })
    }
  },
  mounted () {
    this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    this.axios.get('/chart/getBaseData', { params: { type: 0 } })
      .then((res) => {
        console.log(res)
        this.serviceOption = res.data
        console.log(this.serviceOption)
      })
    this.getData()
  }
}
</script>

<style scoped>
    .chart-item {
        width: 400px;
        height: 240px;
        display: inline-block;
        margin-right: 10px;
        margin-bottom: 10px;
    }
    .title, .search {
        text-align: left;
    }
</style>


